import { RandomBalloon } from './models/RandomBalloon';
import { Balloon } from './models/Balloon';
import { Component, OnInit, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, AfterViewInit {
  randomBalloons: RandomBalloon[] = [];

  balloon: Balloon = null;

  constructor() { }

  ngOnInit() {
    const maxBalloons = this.getRandomInt(10, 30);
    for (let i = 0; i < maxBalloons; i++){
      this.randomBalloons.push(this.createBalloon());
    }
  }

  ngAfterViewInit(): void {
    const balloons: any = document.getElementsByClassName('balloon');

    const that: HomeComponent = this;
    window.addEventListener('mousemove', (event: MouseEvent) => {
      that.mouseMove(event);
    });

    for (const balloon of balloons) {
      balloon.addEventListener('mousedown', (event: MouseEvent) => {
        that.mouseDown(event);
      });

      balloon.addEventListener('mouseup', (event: MouseEvent) => {
        that.mouseUp(event);
      });
    }
  }

   mouseMove(event: MouseEvent): void{
     if (this.balloon){
      this.balloon.style.left = (event.clientX - this.balloon.offsetX) + 'px';
      this.balloon.style.top = (event.clientY - this.balloon.offsetY) + 'px';
    }
  }

  mouseDown(event: MouseEvent): void{
    const balloon = event.target as Balloon;
    console.log('click');
    balloon.offsetX = event.clientX - parseInt(balloon.style.left, 10);
    balloon.offsetY = event.clientY - parseInt(balloon.style.top, 10);
    this.balloon = balloon;
  }

  mouseUp(event: MouseEvent): void{
    console.log('unclick');
    this.balloon = null;
  }

  getRandomInt(min: number, max: number){
    return Math.floor(Math.random() * (max - min)) + min;
  }

  createBalloon(): RandomBalloon{
    const balloon = new RandomBalloon();

    balloon.left = this.getRandomInt(50, window.innerWidth - 170) + 'px';
    balloon.top = this.getRandomInt(0, window.innerHeight - 250) + 'px';

    balloon.animation = (this.getRandomInt(6, 15) * 0.5) + 's';

    switch (this.getRandomInt(0, 5)){
      case 0:
        balloon.color = 'blue';
        break;
      case 1:
        balloon.color = 'purple';
        break;
      case 2:
        balloon.color = 'green';
        break;
      case 3:
        balloon.color = 'yellow';
        break;
      case 4:
        balloon.color = 'red';
        break;
    }

    return balloon;
  }

}
