import { ImageService } from './image.service';
import { SessionManager } from './../shared/SessionManager';
import { Component, OnInit, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-birthday-card',
  templateUrl: './birthday-card.component.html',
  styleUrls: ['./birthday-card.component.css']
})
export class BirthdayCardComponent implements OnInit, AfterViewInit {
  card = null;
  timer = null;

  user = null;
  image = null;

  constructor(private sessionManager: SessionManager, private imageService: ImageService) { }


  ngAfterViewInit(): void {
    this.card = document.getElementById('birthday-card');
  }

  ngOnInit() {
    this.user = this.sessionManager.getUser();
    this.imageService.get(this.user).subscribe(img => this.image = img);
  }


  public openCard(): void {
    this.card.setAttribute('class', 'open-half');
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => {
      this.card.setAttribute('class', 'open-fully');
      this.timer = null;
    }, 1000);
  }

  public closeCard(): void{
    this.card.setAttribute('class', 'close-half');
    if (this.timer){
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => {
      this.card.setAttribute('class', '');
      this.timer = null;
    }, 1000);
  }
}
