import { Injectable } from '@angular/core';
import {Md5} from 'ts-md5/dist/md5';
import { of } from 'rxjs';
import { Observable, throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  private PASSPHRASE = '4659c62f1a98b242d60b81a07c5dc133';

  private VALID_USERS = new Map<string, string>([
                                  ['LU', 'Lu'],
                                  ['LUCIA', 'Lu'],
                                  ['IGNACIO', 'Ignacio'],
                                  ['NACHO', 'Ignacio'],
                                  ['MANUEL', 'Manuel'],
                                  ['PASARON', 'Manuel'],
                                  ['ERIKA', 'Erika y Javi'],
                                  ['ERI', 'Erika y Javi'],
                                  ['JAVI', 'Erika y Javi'],
                                  ['JAVIER', 'Erika y Javi'],
                                  ['BREITOS', 'Erika y Javi'],
                                  ['MARK', 'Marky'],
                                  ['MARKY', 'Marky']
                              ])
  ;

  constructor() { }

  public login(username: string, password: string): Observable<string>{
    username = username.toUpperCase();
    if (this.VALID_USERS.has(username) && this.checkPassword(password)){
      return of(this.VALID_USERS.get(username.toUpperCase()));
    }
    return throwError(`Nombre de usuario o contraseña inválidos`);
  }

  private checkPassword(password: string){
    return this.PASSPHRASE === this.md5(password);
  }

  private md5(str: string){
    const md5 = new Md5();
    return md5.appendStr(str).end();
  }

}
