import {Injectable} from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class SessionManager {
    private USER_KEY = 'USER';
    private SESSION_KEY = 'SESSION';

    private SESSION_DURATION = 300000;

    public store(user: string) {
        sessionStorage.setItem(this.USER_KEY, user);
        sessionStorage.setItem(this.SESSION_KEY, '' + Date.now());
    }

    public getUser() {
        const user: string = sessionStorage.getItem(this.USER_KEY);
        return user;
    }

    private validateSession() {
        const session: string = sessionStorage.getItem(this.SESSION_KEY);

        if (!session){
            return false;
        }
        const millisSession = Number(session);

        return (Date.now() - millisSession) < this.SESSION_DURATION;
    }

    public validate() {
        const validSession: boolean = this.validateSession();
        if (!validSession){
            this.clear();
        }
        return validSession;
    }

    public clear(){
        sessionStorage.clear();
    }
}
