import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ImageService {

  private IMAGES = new Map<string, string>([
    ['Lu', 'assets/lu.jpg'],
    ['Ignacio', 'assets/ignacio.jpg'],
    // ['Manuel', 'Ignacio'],
    ['Erika y Javi', 'assets/erika.jpg'],
    // ['Marky', 'Manuel'],
    ['Default', 'assets/default.jpg']
])
;

constructor() { }

  public get(user: string): Observable<string>{
    if (this.IMAGES.has(user)){
      return of(this.IMAGES.get(user));
    }
    return of(this.IMAGES.get('Default'));
  }

}
